/* form starting stylings ------------------------------- */
.CommonInput_group__22ObE {
    position: relative;
    margin-bottom: 25px;
}

.CommonInput_group__22ObE input {
    background-color: #d0d0d0;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 300px;
    border: none;
    border-bottom: 1px solid #757575;
}

.CommonInput_group__22ObE input:focus {
    outline: none;
}
/*Autofill background*/
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px #d0d0d0 inset !important;
}

/* LABEL ======================================= */
.CommonInput_group__22ObE label {
    color: #646464;
    font-size: 18px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

/* active state */
.CommonInput_group__22ObE input:focus ~ label, .CommonInput_group__22ObE input:valid ~ label {
    top: -20px;
    font-size: 14px;
    color: #3aa251;
}

/* BOTTOM BARS ================================= */
.CommonInput_bar__15uPu {
    position: relative;
    display: block;
    width: 100%;
    margin: 0;
}

.CommonInput_bar__15uPu:before, .CommonInput_bar__15uPu:after {
    content: '';
    height: 2px;
    width: 0;
    bottom: 1px;
    position: absolute;
    background: #3aa251;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.CommonInput_bar__15uPu:before {
    left: 50%;
}

.CommonInput_bar__15uPu:after {
    right: 50%;
}

/* active state */
.CommonInput_group__22ObE input:focus ~ .CommonInput_bar__15uPu:before, .CommonInput_group__22ObE input:focus ~ .CommonInput_bar__15uPu:after {
    width: 50%;
}

/* HIGHLIGHTER ================================== */
.CommonInput_highlight__3kNjV {
    position: absolute;
    height: 60%;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}

/* active state */
.CommonInput_group__22ObE input:focus .CommonInput_highlight__3kNjV {
    animation: CommonInput_inputHighlighter__3cxWz 0.5s ease;
}

/* ANIMATIONS ================ */

@keyframes CommonInput_inputHighlighter__3cxWz {
    from {
        background: #3aa251;
    }
    to {
        width: 0;
        background: transparent;
    }
}
