    height: 30px;
    width: 30px;
    position: absolute;
    content: '✔';
    display: inline-block;
    font-size: 26.66667px;
    text-align: center;
    line-height: 30px;
}
.CommonCheckbox_optionInput__3oNeM:checked::after {
    animation: CommonCheckbox_click-wave__2xti6 0.65s;
    background: #3aa251;
    content: '';
    display: block;
    position: relative;
    z-index: 100;
}
.CommonCheckbox_optionInput__3oNeM.CommonCheckbox_radio__1T0ne {
    border-radius: 50%;
}
.CommonCheckbox_optionInput__3oNeM.CommonCheckbox_radio__1T0ne::after {
    border-radius: 50%;
}

@keyframes CommonCheckbox_click-wave__2xti6 {
    0% {
        height: 30px;
        width: 30px;
        opacity: 0.35;
        position: relative;
    }
    100% {
        height: 200px;
        width: 200px;
        margin-left: -80px;
        margin-top: -80px;
        opacity: 0;
    }
}

